export default "/* # Setup devices width */\n/* # Variables for mobile first approach and strict ranges */\n.services {\n  width: 100%;\n  background: url('bg_body.png');\n  padding: 40px 0;\n  border: 1px solid transparent;\n}\n.services__i {\n  width: 85%;\n  margin: 50px auto;\n  text-align: center;\n}\n@media only screen and (max-width: 991px) {\n  .services__i {\n    width: 100%;\n    padding: 0 20px;\n  }\n}\n.services__list {\n  width: 100%;\n  flex-flow: row wrap;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n}\n.services__title {\n  margin-top: 100px;\n  margin-bottom: 0;\n  font-weight: 500;\n  color: #484848;\n  text-transform: uppercase;\n  font-size: 30px;\n  line-height: 1.5;\n}\n.services__hr {\n  width: 32px;\n  height: 8px;\n  background: #1d6887;\n}\n.services__item {\n  width: 370px;\n  margin: 10px;\n  border: 1px solid transparent;\n}\n.services__link {\n  text-decoration: none;\n  display: block;\n  transition: all 0.3s;\n  width: 100%;\n  height: 100%;\n}\n.services__link:hover {\n  text-decoration: none;\n}\n.services__link:hover .services__desc {\n  transform: scale(1.05, 1.05);\n}\n.services__img {\n  max-width: 100%;\n  max-height: 300px;\n}\n.services__description {\n  margin: 10px 20px;\n  border: 1px solid transparent;\n}\n.services__name {\n  color: #3b4047;\n  text-transform: uppercase;\n  font-weight: 500;\n}\n.services__text {\n  font-size: 16px;\n  line-height: 1.84615;\n  font-weight: 400;\n  color: #6f6f6f;\n  text-align: justify;\n  text-indent: 50px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  max-height: 82px;\n}\n.services__desc {\n  display: flex;\n  flex-flow: row wrap;\n  height: 100%;\n  justify-content: center;\n  box-shadow: 0px 0px 5px 0px rgba(50, 50, 50, 0.75);\n  border: none;\n  background: #f1f1f1;\n  transition: all 0.3s;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"